body, * {
    font-family: 'PT Sans', sans-serif;
}
section {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 15px;
}
section h1 {
    text-align: center;
    font-size: 3em;
    margin: 0 0 30px;
    padding: 0;
    line-height: 1em;
}
section h2 {
    font-size: 1.5em;
    margin: 0;
    line-height: 1em;
}
p.last-updates {
    color: #484848;
    font-size: 12px;
    margin: 0 10px;
}
section .total-updates h2, section .daily-updates h2 {
    margin: 10px;
}
section .totals-container, section .daily-container {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    padding: 30px 0;
    flex-flow: wrap;
}
section .totals-container .updates, section .daily-container .updates {
    min-width: 150px;
    min-height: 150px;
    text-align: center;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    color: #fff;
    padding: 10px;
    margin: 10px;
    border-radius: 4px;
}
section .totals {
    background: #d40000;
}
section .recover {
    background: #007b0a;
}
section .deaths {
    background: #232323;
}
section .admitted {
    background: #e44900;
}
.updates p.quantity {
    font-size: 45px;
    font-weight: bold;
    line-height: 1em;
    margin: 0;
    padding: 0;
}
.updates p.record {
    font-size: 16px;
    padding: 0;
    margin: 0;
}
nav {
    padding: 5px;
    border-top: 1px solid #f5f5f5;
    border-bottom: 1px solid #f5f5f5;
}
nav ul {
    list-style-type: none;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
}
nav ul li {
    display: flex;
}
nav ul li a {
    color: #000;
    font-weight: bold;
    margin: 0 15px;
    text-decoration: none;
    line-height: 1em;
}
nav ul li a:after {
    content: '';
    width: 0px;
    height: 2px;
    background: #000;
    display: block;
    transition: all .75s ease-in-out;
}
nav ul li a:hover::after {
    width: 100%;
    transition: all .75s ease-in-out;
}
header h1 {
    text-align: center;
    color: #bbbbbb;
}
.world-case {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: wrap;
    width: 100%;
}
.country {
    min-width: 200px;
    border: 1px solid #f8f8f8;
    padding: 10px;
    margin: 5px;
}
.country .flag-and-country {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 0 10px;
}
.country .flag-and-country img {
    max-height: 30px;
    width: auto;
}
.flag-and-country p {
    font-weight: bold;
    font-size: 18px;
    margin: 0;
    padding: 0;
    line-height: 1em;
}
.countryInfo p {
    font-size: 14px;
    line-height: 1em;
    padding: 0;
    margin: 5px 0;
    color: #000;
}
.countryInfo p.updated {
    font-size: 12px;
    color: #5f5757;
}
footer {
    background: #eaeaea;
    text-align: center;
}
@media(max-width: 767px){
    header {
        width: 100%;
    }
    header h1 {
        font-size: 2em;
        margin: 0 0 20px;
    }
    nav {
        display: block;
        width: 100%;
    }
    section nav ul {
        width: 100%;
        padding: 0;
    }
    section h1 {
        font-size: 2em;
    }
    section {
        padding: 10px;
    }
    h2 {
        text-align: center;
    }
    
}